@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-light.woff2") format("woff2"), url("../fonts/neuemachina-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-medium.woff2") format("woff2"), url("../fonts/neuemachina-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box;
  font-size: 24px; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"], [type="number"], [type="password"], [type="search"], [type="tel"], [type="text"], [type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

.header {
  display: block;
  padding: 25px 35px; }

@keyframes flag {
  from {
    transform: scale(1); }
  to {
    transform: scale(1.005); } }

@keyframes growArrow {
  from {
    transform: translateY(0); }
  to {
    transform: translateY(0.5%); } }

@keyframes growArrowBlue {
  from {
    transform: translateY(0); }
  to {
    transform: translateY(1%); } }

@keyframes growArrowRed {
  from {
    transform: translateY(0); }
  to {
    transform: translateY(1%); } }

.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  transition: opacity 0.3s linear;
  background-color: #ffffff; }

.modal--hide {
  opacity: 0;
  transition: opacity 0.3s linear;
  z-index: -1; }

.modal__wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 100px 0; }

.modal__logo {
  position: relative;
  top: 50%;
  left: 50%;
  width: 83.34%;
  max-width: 800px;
  margin-bottom: 20px;
  transform: translate(-50%, -50%);
  animation: show 2s; }
  @media (max-width: 1023px) {
    .modal__logo {
      width: 76.4%;
      max-width: 550px; } }

.modal__text {
  position: relative;
  margin: 0 auto;
  font-size: 24px;
  line-height: 22px;
  animation-name: show;
  animation-duration: 5s; }

@keyframes show {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.main__wrapper {
  position: relative;
  display: flex;
  height: calc(100vh - 74px);
  padding: 0 3px; }
  .main__wrapper::after {
    content: "";
    position: absolute;
    width: 65%;
    height: 94%;
    left: 47%;
    bottom: 0;
    transform: translateX(-50%);
    background: url("../img/svg/a.svg") no-repeat; }

.main__title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 58.54%;
  margin-right: 43px;
  margin-top: -100px; }
  .main__title h1 {
    margin: 0;
    font-size: 96px;
    font-weight: 400;
    line-height: 96px;
    color: #2c39f2; }

.main__promo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 36.6%;
  margin-top: -100px; }
  .main__promo .main__svg {
    margin-top: -60px; }
    .main__promo .main__svg #arrow1 {
      animation: growArrow 0.5s ease infinite alternate;
      transform-origin: bottom; }
    .main__promo .main__svg #arrow2 {
      animation: growArrowBlue 0.5s ease infinite alternate;
      animation-delay: 0.3s;
      transform-origin: bottom; }
    .main__promo .main__svg #arrow3,
    .main__promo .main__svg #arrow4 {
      animation: growArrowRed 0.5s ease infinite alternate;
      animation-delay: 0.2s;
      transform-origin: bottom; }
    .main__promo .main__svg #flag {
      animation: flag 1s ease-in-out infinite alternate;
      transform-origin: right; }

.main__svg-white {
  display: none; }

.main__text {
  margin: 0;
  margin-top: 35px;
  padding: 0;
  font-style: normal;
  font-size: 30px;
  font-weight: normal;
  line-height: 40px; }

@media (max-width: 1023px) {
  .main__wrapper {
    position: relative; }
    .main__wrapper::after {
      display: none; }
  .main__promo {
    position: absolute;
    left: -110%;
    width: 100%;
    height: 100vh;
    background-color: #2c39f2;
    margin-top: 0;
    transition: transform 1s ease; }
  .main__promo--show {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100%;
    transform: translate3d(100%, 0, 0);
    transition: transform 1s ease 0.3s; }
  .main__svg-white {
    display: block; }
  .main__svg {
    display: none; }
  .main__text {
    width: 46%;
    color: #ffffff; } }

